.pop-up {
  position: absolute;
  font-size: 1.5rem;
  color: rgb(196, 196, 196);
  user-select: none;

  &_orientation {
    &_horizontal {
      transform: translate(-50%, -30px);
      transition: left 0.2s cubic-bezier(0, 1.73, 0.32, 0.74);
    }

    &_vertical {
      left: 0;
      transform: translate(-100%, -50%);
      padding-right: 15px;
      transition: top 0.2s cubic-bezier(0, 1.73, 0.32, 0.74);
    }
  }
}
